var ItemInput = require("./inventory/ItemInput");
var heroData = require("./hero/heroData");
var HeroOption = require("./hero/HeroOption");
var heroOptionsArray = require("./hero/heroOptionsArray");

var my = HEROCALCULATOR;

//my.prototype.heroData = {};
//my.prototype.itemData = {};
